html {
  scroll-behavior: smooth;
}
.App {
  text-align: center;
  height: 100%;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  height: 100%;
  /* min-height: 120vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.Tab-bar {
  align-items: center;
  justify-content: center;
}
.Start{
  color:white;
}
.Search-form2 {
  background-color: #282c34;
  width: 320px;
  height: 20px;
  border-radius: 4px;
  padding: 10px 20px;
  align-items: center;
  color: white;
}
.Search-form {
  background-color: #282c34;
  width: 320px;
  height: 20px;
  border-radius: 4px;
  padding: 10px 20px;
  align-items: center;
  color: white;
}
.keyterm_tag{
  background-color: #008CBA;
  width: -webkit-fit-content;
  width: fit-content;
  height:auto;
  display: inline-block;
  font-size: 1em;
  text-align: center;
  border: 3px solid black;
  margin: 5px; /* adjust as needed */
  padding: 1px; /* adjust as needed */
  border-radius: 4px;
  border-style: dotted;
  border-color: white;
}
input::placeholder {
  color: white;
  font-size: 1.2em;
  padding: 10px;
  font-weight: bold;
}

.scroll-up {
  position: fixed;
  right: -100px;
  bottom: 150px;
  transition: right 0.5s;
  cursor: pointer;
  background-color: white;
  /* font-size: 20px; */
  padding: 5px;
  margin-bottom: 30px
}
.scroll-up-transition {
  right: 20px;
}

.submit-button {
  background-color: #282c34; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  float: left;
   width: 340px;
   align-items: center;
}

.reset-button {
  position: relative;
  left: 20px;
  background-color: #282c34; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  float: left;
  width: 340px;
  align-items: center;
  padding: 10px 20px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
